import { Global, css } from '@emotion/react'
import { dark, light } from 'assets/styles/Theme'

const defaultStyle = css`
  @font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff')
      format('woff');
    font-weight: 400;
    font-style: normal;
  }

  :root {
    --white: #ffffff;
    --black: #000000;
    --gray0: #eeeeee;
    --gray1: #d9d9d9;
    --gray2: #c2c2c2;
    --gray3: #757575;
    --gray4: #4c4c4c;
    --yellow: #f2cb61;

    --code-default: ${dark.codeBackground};
  }

  [data-theme='dark'] {
    --primary-bg: ${dark.background};

    --second-bg: ${dark.secondBackground};
    --second-color: ${dark.secondColor}
    --highlight: ${dark.highlight};
    --date-color: var(--white);

    --code-color: #cc99cd;
    --code-bg: #2d2d2d;

    --tag-selected-bg: ${dark.highlight};
    --tag-selected-color: var(--white);
    --tag-hover-bg: #4b4e53;
    --tag-hover-color: var(--white);
    --tag-color: var(--white);
    
    --toc-color: var(--white);

    background: var(--primary-bg);
    color: ${dark.defaultColor};

    ul, li {
      color: inherit
    }

    .highlight{
      background: ${dark.highlight};
    }
  }

  [data-theme='light'] {
    --primary-bg: var(--white);
    background: ${light.background};
    color: ${light.defaultColor};

    --highlight: ${light.highlight};
    --date-color: var(--gray4);

    --tag-color: ${light.secondColor};

    --toc-color: var(--gray3)

    ul, li {
      color: ${light.secondColor}
    }
  }

  * {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Pretendard-Regular, serif;
  }

  ul,
  li,
  span,
  svg {
    background: inherit;
  }

  a,
  a:hover {
    background: inherit;
    color: inherit;
    text-decoration: none;
    cursor: pointer;
  }

  main {
    min-height: calc(100vh - 100px);
  }
`

const GlobalStyle = () => {
  return <Global styles={defaultStyle} />
}

export default GlobalStyle
